.markdown-editor {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
  justify-content: space-around;
  -ms-flex-align: stretch;
  align-items: stretch;
  min-height: 500px;
  margin-bottom: 15px;
}
.markdown-editor__editor-container,
.markdown-editor__preview-container {
  -ms-flex: 1 1 50%;
  flex: 1 1 50%;
  max-width: 50%;
  max-height: 100%;
  margin: 0 5px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
}
.markdown-editor__preview,
.markdown-editor__textarea {
  -ms-flex: 1;
  flex: 1 1;
}
.markdown-editor__textarea--dragover {
  border: 1px solid #c9ff00;
  -webkit-box-shadow: 0 0 10px #719ece;
          box-shadow: 0 0 10px #719ece;
}
.markdown-editor__preview img {
  max-width: 100%;
}
.markdown-editor__preview :last-child {
  margin-bottom: 0;
}
body {
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 14px;
  margin: 0;
  padding: 0;
  font-weight: 400;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 300;
  margin: 0.5em 0;
}
a {
  color: #08c;
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}
.container {
  max-width: 1080px;
  margin: auto;
  padding: 1em;
}
#app,
footer {
  border-top: 1px solid #eee;
}
footer {
  text-align: center;
  margin-top: 20px;
  padding: 20px 0;
}



